const MDCComponent = goog.require('mdc.base.component');

const MDCTextFieldLabelAdapter = goog.require('mdc.textfield.label.adapter');
const MDCTextFieldLabelFoundation = goog.require('mdc.textfield.label.foundation');

/**
 * @extends {MDCComponent<!MDCTextFieldLabelFoundation>}
 * @final
 */
class MDCTextFieldLabel extends MDCComponent {
  /**
   * @param {!Element} root
   * @return {!MDCTextFieldLabel}
   */
  static attachTo(root) {
    return new MDCTextFieldLabel(root);
  }

  /**
   * @return {!MDCTextFieldLabelFoundation}.
   */
  get foundation() {
    return this.foundation_;
  }

  /**
   * @return {!MDCTextFieldLabelFoundation}
   */
  getDefaultFoundation() {
    return new MDCTextFieldLabelFoundation(/** @type {!MDCTextFieldLabelAdapter} */ (Object.assign({
      addClass: (className) => this.root_.classList.add(className),
      removeClass: (className) => this.root_.classList.remove(className),
    })));
  }
}

exports = {
 MDCTextFieldLabel,
 MDCTextFieldLabelFoundation,
};